import TextFilter from "./TextFilter";
import Typography from "@mui/material/Typography";
import NumberFilter from "./NumberFilter";
import React, {useEffect} from "react";
import {Checkbox, Divider, Drawer, IconButton} from "@mui/material";
import CloseIcon from "@mui/icons-material/Close";
import _ from "lodash";
import SortByDropdown, {SortIssueComparator} from "./SortByDropdown";
import FilterListIcon from "@mui/icons-material/FilterList";
import SortIcon from "@mui/icons-material/Sort";
import {IssueObject} from "../types";
import {NUMBER_OF_ISSUES_PER_PAGE} from "../pages/Home";

interface SideBarFilterProps {
    drawerOpen: boolean;
    firstRender: React.MutableRefObject<boolean>;
    setDrawerOpen: React.Dispatch<React.SetStateAction<boolean>>;
    setResults: React.Dispatch<React.SetStateAction<IssueObject[]>>;
    setTotalPageNumber: React.Dispatch<React.SetStateAction<number>>;
    initialResults: IssueObject[];
    sortFunctionSetter: React.Dispatch<
        React.SetStateAction<SortIssueComparator | null>
    >;
}

export interface FilterOptionWithCount {
    option: string;
    count: number;
}

function getFilterOptionCounts(options: string[]) {
    const optionCounts = Object.entries(_.countBy(options));
    return _.sortBy(optionCounts, ([_, count]) => count)
        .reverse()
        .map(([option, count]) => {
            return {
                option: option,
                count: count,
            } satisfies FilterOptionWithCount;
        });
}

export default function SideFilterBar(props: SideBarFilterProps) {
    // Filters
    const [languages, setLanguages] = React.useState([] as string[]);
    const [labels, setLabels] = React.useState([] as string[]);
    const [repository, setRepositories] = React.useState([] as string[]);
    const [initialStarRange, setInitialStarRange] = React.useState(
        [] as number[],
    );
    const [starRange, setStarRange] = React.useState([] as number[]);
    const [noAssignees, setNoAssignees] = React.useState(false);

    const [filteredResults, setFilteredResults] = React.useState([] as IssueObject[]);

    const repoProgLangs = filteredResults
        .flatMap((result) => result.Issue.issue_repo.repo_langs.Nodes)
        .map((node) => (node?.repo_prog_language ? node.repo_prog_language : ""))
        .filter((lang) => lang.trim().length > 0);

    const issueLabels = filteredResults
        .flatMap((result) => result.Issue.issue_labels.Nodes)
        .map((node) => (node?.label_name ? node.label_name : ""))
        .filter((label) => label.trim().length > 0);

    const repos = filteredResults
        .map((result) => result.Issue.issue_repo.repo_name)
        .filter((repo) => repo.trim().length > 0);

    const getFiltersFromStorage = (data: IssueObject[]) => {
        const storedLanguageFilter = localStorage.getItem("languageFilter");
        if (storedLanguageFilter && JSON.parse(storedLanguageFilter).length > 0) {
            setLanguages(JSON.parse(storedLanguageFilter!));
        } else {
            setLanguages([]);
        }

        const storedLabelsFilter = localStorage.getItem("labelsFilter");
        if (storedLabelsFilter && JSON.parse(storedLabelsFilter).length > 0) {
            setLabels(JSON.parse(storedLabelsFilter!));
        } else {
            setLabels([]);
        }

        const storedRepoFilter = localStorage.getItem("repositoryFilter");
        if (storedRepoFilter && JSON.parse(storedRepoFilter).length > 0) {
            setRepositories(JSON.parse(storedRepoFilter!));
        } else {
            setRepositories([]);
        }

        const storedStarRange = localStorage.getItem("starRange");
        const issueStars = data.map((issue) => issue.Issue.issue_repo.repo_stars);

        setInitialStarRange([_.min(issueStars) ?? 0, _.max(issueStars) ?? 0]);
        if (storedStarRange && JSON.parse(storedStarRange).length > 0) {
            setStarRange(JSON.parse(storedStarRange!));
        } else {
            setStarRange([_.min(issueStars) ?? 0, _.max(issueStars) ?? 0]);
        }

        const storedNoAssignees = localStorage.getItem("noAssignees");
        if (storedNoAssignees && JSON.parse(storedNoAssignees)) {
            setNoAssignees(true);
        }
    };

    useEffect(() => {
        getFiltersFromStorage(props.initialResults);
    }, [props.initialResults]);

    useEffect(() => {
        if (!props.firstRender.current) {
            localStorage.setItem("languageFilter", JSON.stringify(languages));
            localStorage.setItem("labelsFilter", JSON.stringify(labels));
            localStorage.setItem("repositoryFilter", JSON.stringify(repository));
            localStorage.setItem("starRange", JSON.stringify(starRange));
            localStorage.setItem("noAssignees", JSON.stringify(noAssignees));
        }

        const filteredData = props.initialResults
            .filter((issueObject) =>
                languages.length > 0
                    ? _.intersection(
                    issueObject.Issue.issue_repo.repo_langs.Nodes?.map(
                        (node) => node.repo_prog_language,
                    ) ?? [],
                    languages,
                ).length > 0
                    : true,
            )
            .filter((issueObject) =>
                labels.length > 0
                    ? _.intersection(
                    issueObject.Issue.issue_labels.Nodes?.map(
                        (node) => node.label_name,
                    ) ?? [],
                    labels,
                ).length > 0
                    : true,
            )
            .filter((issueObject) =>
                repository.length > 0
                    ? repository.includes(issueObject.Issue.issue_repo.repo_name)
                    : true,
            )
            .filter((issueObject) =>
                starRange.length > 0
                    ? issueObject.Issue.issue_repo.repo_stars >= starRange[0] &&
                    issueObject.Issue.issue_repo.repo_stars <= starRange[1]
                    : true,
            )
            .filter((issueObject) =>
                noAssignees
                    ? issueObject.Issue.Assignees.issue_assignees_count === 0
                    : true
            );

        props.setResults(filteredData);
        setFilteredResults(filteredData);
        props.setTotalPageNumber(
            Math.ceil(filteredData.length / NUMBER_OF_ISSUES_PER_PAGE),
        );
    }, [languages, labels, repository, starRange, noAssignees]);

    const filters = (
        <div className="flex gap-4 flex-col pb-4">
            <div className="flex gap-4 items-center">
                <FilterListIcon
                    color="primary"
                    sx={{fontSize: "23px"}}
                ></FilterListIcon>
                <Typography variant="h6" className="select-none" sx={{color: "primary.main"}}>
                    Filters
                </Typography>
            </div>
            <TextFilter
                title="Repo Programming Language"
                setOptions={languages}
                options={getFilterOptionCounts(repoProgLangs)}
                setter={setLanguages}
            ></TextFilter>
            <TextFilter
                title="Issue Label"
                setOptions={labels}
                options={getFilterOptionCounts(issueLabels)}
                setter={setLabels}
            ></TextFilter>
            <TextFilter
                title="Repository"
                setOptions={repository}
                options={getFilterOptionCounts(repos)}
                setter={setRepositories}
            ></TextFilter>

            <div>
                <NumberFilter
                    title="Repo Stars"
                    rangeSetter={setStarRange}
                    range={starRange}
                    initialMax={initialStarRange[1]}
                    initialMin={initialStarRange[0]}
                ></NumberFilter>

                <div className="flex items-center gap-2">
                    <Typography variant="caption" className="select-none text-nowrap" sx={{ color: "primary.main" }}>
                        Only show issues with no assignee
                    </Typography>
                    <Checkbox size="small" checked={noAssignees} onChange={() => setNoAssignees(!noAssignees)}/>
                </div>
            </div>

            <Divider></Divider>

            <div className="flex gap-4 items-center">
                <SortIcon color="primary" sx={{fontSize: "23px"}}></SortIcon>
                <Typography variant="h6" className="select-none" sx={{color: "primary.main"}}>
                    Sort
                </Typography>
            </div>
            <SortByDropdown
                sortFunctionSetter={props.sortFunctionSetter}
            ></SortByDropdown>
        </div>
    );

    if (props.drawerOpen) {
        return (
            <Drawer
                open={true}
                onClose={() => props.setDrawerOpen(false)}
                sx={{
                    "& .MuiDrawer-paper": {
                        width: "85%",
                        padding: "24px",
                    },
                }}
            >
                <div className="flex justify-end pb-4">
                    <IconButton
                        color="primary"
                        onClick={() => props.setDrawerOpen(false)}
                        sx={{
                            padding: 0,
                        }}
                    >
                        <CloseIcon/>
                    </IconButton>
                </div>
                <div className="px-4">{filters}</div>
            </Drawer>
        );
    }

    return (
        <div className="sticky top-8 h-full max-h-[80vh] overflow-y-auto overflow-x-hidden">
            {filters}
        </div>
    );
}
