import Box from "@mui/material/Box";
import * as React from "react";
import IssueCard from "./IssueCard";
import {Divider} from "@mui/material";
import {IssueObject} from "../types";

interface IssuesProps {
    issues: IssueObject[];
}

export default function Issues(props: IssuesProps) {
    const divider = <Divider/>;
    const mapToIssueCard = (issue: IssueObject, index: number) => {
        if (index > 0 && index % 5 === 0) {
          return <div key={"ad_" + index}>
            <script async
                    src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-1130124846637992"
                    crossOrigin="anonymous"></script>
            <ins className="adsbygoogle block"
                 data-ad-format="fluid"
                 data-ad-layout-key="-fb+5w+4e-db+86"
                 data-ad-client="ca-pub-1130124846637992"
                 data-ad-slot="5362633000"></ins>
            <script>
              (adsbygoogle = window.adsbygoogle || []).push({});
            </script>
            {divider}
          </div>
        } else {
          return <div key={"issue_" + index}>
            <IssueCard issue={issue}></IssueCard>
            {divider}
          </div>
        }
    }

  return (
      <Box>
        {props.issues.map((issue, index) => {
          return mapToIssueCard(issue, index);
        })}
      </Box>
  );
}
